import {Component, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {Select, Store} from '@ngxs/store';
import {AppState} from '../../shared/states/app-state/app-state';
import {Observable} from 'rxjs';
import {Channel} from '../../shared/entities/channel';
import {AddMessage, SaveMessageToChannel} from '../../shared/states/app-state/app-state.actions';
import {IonContent} from '@ionic/angular';
import {MessageVm} from '../../shared/view-models/message-vm';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.page.html',
  styleUrls: ['./chat.page.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ChatPage implements OnInit {

  private scrollDuration = 300;
  private waitBeforeScroll = 100;

  @Select(AppState.currentChannel) currentChannel$: Observable<Channel>;
  @Select(AppState.currentChatMessage) currentChatMessage$: Observable<string>;
  @Select(AppState.currentChatMessages) currentMessages$: Observable<MessageVm[]>;

  @ViewChild(IonContent, {static: true}) content: IonContent;

  message = '';
  private currentChannelId: string = null;

  constructor(
    private store: Store
  ) {
  }

  ngOnInit() {
    this.currentChannel$.subscribe(channel => {
      if (this.currentChannelId != null) {
        this.store.dispatch(new SaveMessageToChannel(this.currentChannelId, this.message));
        this.message = '';
        this.currentChannelId = channel.id;

        // TODO: Scroll to Position
        // this.scrollTo()
        this.scrollToBottom();
      }
      this.currentChannelId = channel.id;
    });
    this.currentChatMessage$.subscribe(chatMessage => {
      this.message = chatMessage;
    });
  }

  ionViewDidEnter() {
    // TODO: Get last chat position and scroll to
    // this.scrollTo()
    this.scrollToBottom();
  }

  ionViewWillLeave() {
    this.store.dispatch(new SaveMessageToChannel('d', this.message));
  }

  identifyMessage(index: number, item: MessageVm) {
    return item.id;
  }

  private scrollToBottom() {
    new Promise(resolve => setTimeout(resolve, this.waitBeforeScroll)).then(value => {
      this.content.scrollToBottom(this.scrollDuration);
    });
  }

  private scrollTo(position: number) {
    new Promise(resolve => setTimeout(resolve, this.waitBeforeScroll)).then(value => {
      this.content.scrollToPoint(null, position, this.scrollDuration);
    });
  }

  sendMessage() {
    this.store.dispatch(new AddMessage(this.message));
    this.message = '';
    this.scrollToBottom();

  }
}
